
.portfolio {
  padding: 5em 0;
}
.portfolio .portfolio__inner {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    min-height: auto;
    max-width: 65rem;
    margin: 0 auto;
    box-sizing: border-box;
}
.portfolio .portfolio__inner .item {
      box-shadow: 0 0 5rem -2rem #000;
      display: block;
      -ms-flex-item-align: start;
          align-self: flex-start;
      margin: .2rem;
      min-width: 20rem;
      height: 15rem;
      overflow: hidden;
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      overflow: hidden;
      position: relative;
      border-radius: .3rem;
      background: #97dcac;
      list-style: none;
      color: #333;
}
.portfolio .portfolio__inner .item .item__image,
      .portfolio .portfolio__inner .item img {
        display: block;
        height: 100%;
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        transition: .3s;
}
.portfolio .portfolio__inner .item .item__summary {
        width: 100%;
        background-color: #97dcac;
        line-height: 5rem;
        position: absolute;
        margin: 0;
        transition: .5s;
        height: inherit;
        opacity: 0;
        bottom: 0;
        -webkit-transform: translateY(5rem);
                transform: translateY(5rem);
}
.portfolio .portfolio__inner .item .item__summary .summary__text {
          text-align: left;
          padding: 0 2rem;
}
.portfolio .portfolio__inner .item .item__summary h3,
        .portfolio .portfolio__inner .item .item__summary p {
          max-width: 50rem;
          margin: 0 auto;
}
.portfolio .portfolio__inner .item:first-child {
        min-width: 30rem;
        -webkit-box-flex: 20;
            -ms-flex-positive: 20;
                flex-grow: 20;
}
.portfolio .portfolio__inner .item:nth-child(6) {
        min-width: 30rem;
}
.portfolio .portfolio__inner .item:last-child {
        min-width: 2rem;
}
.portfolio .portfolio__inner .item:hover .item__image,
      .portfolio .portfolio__inner .item:hover img {
        -webkit-transform: translateY(-5rem);
                transform: translateY(-5rem);
}
.portfolio .portfolio__inner .item:hover .item__summary {
        opacity: 1;
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
}
@media (max-width: 50em) {
.portfolio .portfolio__inner {
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      padding: 1rem;
}
.portfolio .portfolio__inner .item:first-child, .portfolio .portfolio__inner .item:nth-child(6), .portfolio .portfolio__inner .item:last-child {
        min-width: 20rem;
        -webkit-box-flex: 1;
            -ms-flex-positive: 1;
                flex-grow: 1;
}
}



