
footer {
  background: #3D3D49;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1rem;
  border-top: 0.1rem solid #97dcac;
  box-shadow: 0 0 10rem -2rem #000;
}
footer h4 {
    width: 100%;
    text-align: center;
    color: #ededed;
    vertical-align: middle;
}
footer svg {
    width: 2em;
    margin-right: .5em;
}
footer svg path,
    footer svg polygon {
      fill: #97dcac;
}
footer .contact .contact--mobile .contact__nav ul {
    list-style-type: none;
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
footer .contact .contact--mobile .contact__nav ul li {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      box-shadow: 0 0 10rem -2rem #000;
      margin: .1em;
      display: inline-block;
      background-color: rgba(151, 220, 172, 0.1);
      border-radius: 0.3em;
      overflow: hidden;
}
footer .contact .contact--mobile .contact__nav ul a {
      transition: .3s;
      padding: .7rem;
      color: #663231;
      text-decoration: none;
      color: rgba(255, 255, 255, 0.7);
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      text-align: left;
