
.header {
  top: 0;
  background-color: transparent;
  color: white;
  position: fixed;
  width: 100%;
  transition: .3s;
  z-index: 9;
}
.header a {
    text-decoration: none;
}
.header .header__inner {
    max-width: 80rem;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0.5rem 1rem;
}
.header .header__inner h1 {
      color: #97dcac;
      font-size: 1.2rem;
      text-transform: uppercase;
      font-weight: bold;
      text-align: left;
}
.header .header__inner h1 small {
        font-size: 1rem;
        color: #aaa;
}
.header .header__inner .header__nav {
      width: 100%;
      height: inherit;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.header .header__inner .header__nav ul {
        box-sizing: border-box;
        width: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: end;
            -ms-flex-pack: end;
                justify-content: flex-end;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        height: 100%;
        padding: 0;
        margin: 0;
        list-style: none;
}
.header .header__inner .header__nav ul li {
          margin: 1rem;
}
.header .header__inner .header__nav ul li a {
            display: block;
            padding: 0.5rem 1rem;
            text-decoration: none;
            color: #aaa;
            font-weight: bold;
            text-transform: uppercase;
            font-size: .8rem;
            transition: .3s;
}
.header .header__inner .header__nav ul li a:hover, .header .header__inner .header__nav ul li a.nav--active {
              color: #97dcac;
}
.header .header__inner .header__nav ul li.mobile_only {
            display: none;
}
.header .header__inner .header__nav--aside {
      position: absolute;
      top: 0;
      background: rgba(61, 61, 73, 0.95);
      width: 0%;
      padding: 0;
      margin: 0;
      right: 0;
      display: inline-block;
      height: 100vh;
      transition: .3s;
      opacity: 0;
}
.header .header__inner .header__nav--aside ul {
        list-style: none;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        height: 100%;
        margin: 0;
        box-sizing: border-box;
        position: relative;
}
.header .header__inner .header__nav--aside ul li a {
          color: white;
          font-size: 2rem;
          text-transform: uppercase;
}
.header .header__inner .header__nav--aside ul li a:hover, .header .header__inner .header__nav--aside ul li a.nav--active {
            color: #97dcac;
}
.header .header__inner .header__nav--aside ul li.mobile_only {
          position: absolute;
          top: 1rem;
          right: 1rem;
}
.header .header__inner .header__nav--aside ul li.mobile_only a {
            font-size: 1rem;
            text-transform: capitalize;
}
.header .header__inner .header__nav--aside.nav--open {
        opacity: 1;
        height: 100vh;
        width: 100%;
}
.header.header--filled {
    background-color: #3D3D49;
    box-shadow: 0 0 2em #333;
}
@media (max-width: 50rem) {
.header .header__inner {
      padding: 0.8rem 1rem;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.header .header__inner .header__logo {
        max-width: 75%;
}
.header .header__inner .header__logo h1 {
          font-size: 1rem;
          margin: 0;
}
.header .header__inner .header__nav {
        max-width: 25%;
}
.header .header__inner .header__nav ul li {
          display: none;
}
.header .header__inner .header__nav ul li.mobile_only {
            margin: 0;
            display: block;
}
}



