
.poster {
  height: 45rem;
  background-color: #3D3D49;
  background-repeat: no-repeat;
  background-size: cover;
}
.poster canvas {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    z-index: 0;
}
.poster .poster__inner {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    height: 100%;
    box-sizing: border-box;
    position: relative;
}
.poster .poster__inner .poster__intro {
      transition: .3s;
      padding: 1em;
      box-sizing: border-box;
      max-width: 30rem;
      position: absolute;
      z-index: 1;
}
.poster .poster__inner .poster__intro h1,
      .poster .poster__inner .poster__intro h2 {
        text-align: left;
        color: white;
        text-shadow: 0 0 10px #333;
        text-transform: uppercase;
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
        transition: .3s;
        max-width: 30em;
}
.poster .poster__inner .poster__intro h2 {
        margin: auto;
        margin-top: 1em;
        margin-bottom: 1em;
        font-weight: normal;
        position: relative;
        z-index: 1;
}
.poster .poster__inner .poster__intro h3 {
        text-align: left;
        color: white;
        text-shadow: 0 0 10px #333;
}
.poster .poster__inner .poster__intro .button {
        display: block;
        margin: 2rem auto;
        padding: 1rem;
        text-decoration: none;
        color: white;
        width: 10em;
        text-transform: uppercase;
        border: 0.2rem solid white;
        background-color: transparent;
        border-radius: .3rem;
        transition: .3s;
        font-weight: bold;
}
.poster .poster__inner .poster__intro .button:hover {
          background: white;
          color: #333;
}
@media (max-width: 50em) {
.poster .poster__inner .poster__intro h1,
    .poster .poster__inner .poster__intro h2 {
      -webkit-transform: scale(1);
              transform: scale(1);
}
.poster.poster__page {
      height: 25rem;
}
.poster.poster__home .poster__inner .poster__intro {
      top: 8rem;
}
}



