
html,
body,
#app {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
#app {
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

/*green #97dcac*/
.section-header {
  width: 100%;
  font-size: 4em;
  font-weight: bold;
  text-align: center;
  color: #663231;
  margin: .5em 0;
}
blockquote {
  border-left: .3em solid #ededed;
  padding-left: 1em;
  color: #999;
  font-style: italic;
}
.title {
  width: 100%;
  font-weight: bold;
  text-align: center;
  color: #663231;
  margin: 2rem 0;
  text-transform: uppercase;
}
.subtitle {
  max-width: 32rem;
  margin: 3rem auto;
  text-align: left;
  display: block;
  min-height: 4rem;
  padding: 2rem;
}
p,
li {
  line-height: 175%;
}
.hidden {
  display: none;
}



