
.about {
  padding: 5em 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #ededed;
  max-width: 80rem;
  margin: 0 auto;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border-radius: .3rem;
  box-shadow: 0 0 10rem -2rem #000;
}
.about .figure {
    width: 10rem;
    height: 10rem;
    margin: auto;
    overflow: hidden;
}
.about .figure.figure--circle {
      border-radius: 100%;
      border-radius: 100%;
}
.about .figure.figure--circle .figure__image {
        width: 100%;
        height: auto;
}
.about .intro__text {
    max-width: 32rem;
    margin: 0 auto;
    color: #333;
    padding: 2rem;
}
.about .intro__text .text__title {
      font-style: normal;
      text-align: center;
      margin-top: 0;
      text-transform: uppercase;
}
.about .intro__text .text__paragraph {
      font-style: normal;
      text-align: left;
}
.about .intro__text .text__link {
      text-align: center;
      font-style: normal;
      color: #663231;
      text-decoration: none;
      font-weight: bold;
      transition: .3s;
      padding: 1rem;
      display: block;
}
.about .intro__text .text__link:hover {
        color: #97dcac;
}
@media (min-width: 50em) {
.about .intro {
      padding: 10em 0;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.about .intro .intro__image {
        width: 20em;
}
.about .intro .intro__text {
        max-width: 32em;
        margin: auto;
