
.hidden {
  opacity: 0 !important;
}
.skills {
  padding: 5em 0;
  width: 100%;
  position: relative;
  background: #ededed;
  transition: 3s;
}
.skills .carousel {
    outline: 0;
    padding: 1rem;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    cursor: -webkit-grab;
    cursor: grab;
    /* dots are lines */
}
.skills .carousel .carousel-cell {
      margin: 0 .5rem;
      box-shadow: 0 0 1.5rem 0rem #ededed;
      width: 10rem;
      height: 6rem;
      border-radius: .3rem;
      color: white;
      font-weight: bold;
      background: #97dcac;
      transition: .3s;
      opacity: 0.5;
      -webkit-transform: scale(0.8);
              transform: scale(0.8);
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-pack: space-evenly;
          -ms-flex-pack: space-evenly;
              justify-content: space-evenly;
      padding: 1rem;
}
.skills .carousel .carousel-cell span {
        margin-top: 1rem;
}
.skills .carousel .carousel-cell.is-selected {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
}
.skills .carousel .flickity-viewport {
      transition: height 0.2s;
      margin-bottom: 1rem;
}
.skills .carousel .flickity-page-dots {
      bottom: -22px;
}
.skills .carousel .flickity-page-dots .dot {
      height: 4px;
      width: 40px;
      margin: 0;
      border-radius: 0;
}
.skills .carousel .flickity-page-dots .dot.is-selected {
      background: white;
}



