
.errorpage .errorpage__nav ul {
  padding: 0;
  list-style: none;
}
.errorpage .errorpage__nav ul li a {
    display: inline-block;
    font-weight: bold;
    font-size: 1.3rem;
    text-decoration: none;
    transition: .3s;
    color: #663231;
    margin: .4rem 0;
}
.errorpage .errorpage__nav ul li a:hover {
      color: #97dcac;
}



